.back-btn-group {
  position: relative;
  margin-bottom: rem(50);
  &:before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    border-top: rem(1) solid $color-line; }
  &:after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: rem(6);
    height: rem(6);
    border: rem(1) solid;
    border-color: transparent transparent $color-line $color-line;
    transform-origin: center center;
    transform: translateY(-45%) rotate(45deg); } }
