.header {
  position: relative;
  margin-bottom: rem(80); }

.header__bar {
  position: absolute;
  background: $color-text-main;
  height: rem(8);
  top: 0;
  right: 0;
  left: $outer-padding; }

.header__content {
  display: flex;
  justify-content: space-between;
  padding-top: rem(30); }

.header__logo {
  display: inline-block;
  @include f(m-semibold, 10, 15, 500);
  text-transform: uppercase;
  color: inherit;
  text-decoration: none; }

.header__logo-second-line {
  display: block;
  margin-top: rem(3);
  @include f(p-regular, 12, 15);
  color: $color-text-grey; }

.header__text {
  margin-top: rem(30);
  font-size: rem(14);
  line-height: (20/14); }

//.header__login-group

.header__btn-separator {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  border-top: rem(1) solid;
  width: rem(50); }
