.main-btn {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  @include f(m-semibold, 10, 15, 500);
  text-transform: uppercase;
  color: inherit;
  text-decoration: none;
  text-align: center;
  padding-left: em(15, 15);
  padding-right: em(8, 15);
  white-space: nowrap;
  cursor: pointer;
  transition: opacity 1s, visibility 1s;
  @include gpu;
  &:hover {
    .main-btn__hover-wrap {
      width: 100%;
      visibility: visible;
      opacity: 1;
      transition: width .35s, visibility 0s linear 0s, opacity 0s linear 0s; } }
  &--align-left {
    margin-left: em(-15, 15); }
  &--align-right {
    margin-right: em(-8, 15); }
  &--regular {
    @include f(m-regular, null, null, 250); } }

.main-btn__hover-wrap {
  position: absolute;
  z-index: 2;
  left: 0;
  top: 0;
  width: 0;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
  transition: width .35s, visibility 0s linear .35s, opacity 0s linear .35s;
  &.hidden {
    opacity: 0;
    visibility: hidden; }

  .dark &, .log-in-state & {
    color: white;
    .main-btn__hover {
      background-color: white;
      color: $color-text-main; } } }

.main-btn__hover {
  background-color: $color-text-main;
  color: white;
  padding-left: em(15, 15);
  padding-right: em(8, 15);
  box-sizing: border-box; }


