.video-container {
  position: relative;
  background: $color-text-main no-repeat center;
  background-size: cover;
  color: white;
  height: rem(400); }

.video-container__content {
  position: absolute;
  top: 50%;
  left: $inner-padding + $outer-padding;
  transform: translateY(-50%);
  margin-top: -0.5px; }
