@include font("montserrat-black", "montserrat/montserrat-black-webfont");
@include font("montserrat-bold", "montserrat/montserrat-bold-webfont");
@include font("montserrat-extrabold", "montserrat/montserrat-extrabold-webfont");
@include font("montserrat-light", "montserrat/montserrat-light-webfont");
@include font("montserrat-regular", "montserrat/montserrat-regular-webfont");
@include font("montserrat-semibold", "montserrat/montserrat-semibold-webfont");
@include font("montserrat-ultralight", "montserrat/montserrat-ultralight-webfont");

@include font("proxima_nova_black_italic", "proxima/mark_simonson_-_proxima_nova_black_italic-webfont");
@include font("proxima_nova_black", "proxima/mark_simonson_-_proxima_nova_black-webfont");
@include font("proxima_nova_bold_italic", "proxima/mark_simonson_-_proxima_nova_bold_italic-webfont");
@include font("proxima_nova_extrabold_italic", "proxima/mark_simonson_-_proxima_nova_extrabold_italic-webfont");
@include font("proxima_nova_light", "proxima/mark_simonson_-_proxima_nova_light-webfont");
@include font("proxima_nova_regular", "proxima/mark_simonson_-_proxima_nova_regular-webfont");
@include font("proxima_nova_semibold", "proxima/mark_simonson_-_proxima_nova_semibold-webfont");
@include font("proxima_nova_thin_italic", "proxima/mark_simonson_-_proxima_nova_thin_italic-webfont");
@include font("proxima_nova_thin", "proxima/mark_simonson_-_proxima_nova_thin-webfont");

@include font("fontawesome", "awesome/fontawesome-webfont");

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
  $font-family: null;

  @if $name == "m-black" {
    $font-family: "montserrat-black"; }
  @else if $name == "m-bold" {
    $font-family: "montserrat-bold"; }
  @else if $name == "m-extrabold" {
    $font-family: "montserrat-extrabold"; }
  @else if $name == "m-light" {
    $font-family: "montserrat-light"; }
  @else if $name == "m-regular" {
    $font-family: "montserrat-regular"; }
  @else if $name == "m-semibold" {
    $font-family: "montserrat-semibold"; }
  @else if $name == "m-ultralight" {
    $font-family: "montserrat-ultralight"; }

  @else if $name == "p-black_italic" {
    $font-family: "proxima_nova_black_italic"; }
  @else if $name == "p-black" {
    $font-family: "proxima_nova_black"; }
  @else if $name == "p-bold_italic" {
    $font-family: "proxima_nova_bold_italic"; }
  @else if $name == "p-extrabold_italic" {
    $font-family: "proxima_nova_extrabold_italic"; }
  @else if $name == "p-light" {
    $font-family: "proxima_nova_light"; }
  @else if $name == "p-regular" {
    $font-family: "proxima_nova_regular"; }
  @else if $name == "p-semibold" {
    $font-family: "proxima_nova_semibold"; }
  @else if $name == "p-thin_italic" {
    $font-family: "proxima_nova_thin_italic"; }
  @else if $name == "p-thin" {
    $font-family: "proxima_nova_thin"; }

  @else if $name == "fa" {
    $font-family: "fontawesome"; }

  font-family: $font-family, Helvetica Neue, Helvetica, Arial, sans-serif;
  @if $font-family != null {
    font-weight: normal; }
  @if $font-size != null {
    font-size: rem($font-size); }
  @if $line-height != null {
    line-height: $line-height/$font-size; }
  @if $letter-spacing != null {
    letter-spacing: ($letter-spacing / 1000) * 1em; } }
